export default "/* Basic styling */\n* {\n  box-sizing: border-box;\n  padding: 0;\n  margin: 0;\n}\nbody {\n  font-family: sans-serif;\n}\nnav {\n  background: #222 !important;\n  padding: 5px 20px;\n}\nul {\n  list-style-type: none;\n}\na {\n  color: white;\n  text-decoration: none;\n}\nhr {\n  border-left: 6px solid green;\n  height: 500px;\n}\na:hover {\n  text-decoration: underline;\n}\n.logo a:hover {\n  text-decoration: none;\n}\n.main-logo {\n  width: 18%;\n}\n.email {\n  display: block;\n}\n.menu li {\n  font-size: 16px;\n  padding: 15px 5px;\n  white-space: nowrap;\n}\n.logo a,\n.toggle a {\n  font-size: 20px;\n}\n.button {\n  background-color: #222222;\n  border-color: #222222;\n  background: transparent;\n}\n.button.secondary {\n  border-bottom: 1px #444 solid !important;\n}\n/* Mobile menu */\n.menu {\n  display: -webkit-box;\n  display: flex;\n  flex-wrap: wrap;\n  -webkit-box-pack: justify;\n          justify-content: space-between;\n  -webkit-box-align: center;\n          align-items: center;\n}\n.toggle {\n  -webkit-box-ordinal-group: 2;\n          order: 1;\n}\n.item.button {\n  -webkit-box-ordinal-group: 3;\n          order: 2;\n}\n.item {\n  width: 100%;\n  text-align: center;\n  -webkit-box-ordinal-group: 4;\n          order: 3;\n  display: none;\n}\n.item.active {\n  display: block;\n}\n/* Navbar Toggle */\n.toggle {\n  cursor: pointer;\n}\n.bars {\n  background: #999 !important;\n  display: inline-block;\n  height: 2px;\n  position: relative;\n  -webkit-transition: background 0.2s ease-out;\n  transition: background 0.2s ease-out;\n  width: 18px;\n}\n.bars:before,\n.bars:after {\n  background: #999 !important;\n  content: \"\";\n  display: block;\n  height: 100%;\n  position: absolute;\n  -webkit-transition: all 0.2s ease-out;\n  transition: all 0.2s ease-out;\n  width: 100%;\n}\n.bars:before {\n  top: 5px;\n}\n.bars:after {\n  top: -5px;\n}\n/* Tablet menu */\n@media all and (min-width: 468px) {\n  .menu {\n    -webkit-box-pack: center;\n            justify-content: center;\n  }\n\n  .logo {\n    -webkit-box-flex: 1;\n            flex: 1;\n  }\n\n  .main-logo {\n    width: 15%;\n  }\n\n  .item.button {\n    width: auto;\n    -webkit-box-ordinal-group: 2;\n            order: 1;\n    display: block;\n  }\n\n  .toggle {\n    -webkit-box-ordinal-group: 3;\n            order: 2;\n  }\n\n  .button.secondary {\n    border: 0;\n  }\n\n  .button a {\n    padding: 7.5px 15px;\n    border: 1px #006d6d solid !important;\n  }\n\n  .button.secondary a {\n    background: transparent;\n  }\n\n  .button a:hover {\n    text-decoration: none;\n    -webkit-transition: all 0.25s;\n    transition: all 0.25s;\n  }\n\n  .button:not(.secondary) a:hover {\n    background: #006d6d !important;\n    border-color: #005959 !important;\n  }\n\n  .button.secondary a:hover {\n    color: #ddd;\n  }\n}\n/* Desktop menu */\n@media all and (min-width: 768px) {\n  .item {\n    display: block;\n    width: auto;\n  }\n\n  .toggle {\n    display: none;\n  }\n\n  .logo {\n    -webkit-box-ordinal-group: 1;\n            order: 0;\n  }\n\n  .main-logo {\n    width: 10%;\n  }\n\n  .item {\n    -webkit-box-ordinal-group: 2;\n            order: 1;\n  }\n\n  .button {\n    -webkit-box-ordinal-group: 3;\n            order: 2;\n  }\n\n  .menu li {\n    padding: 15px 10px;\n  }\n\n  .menu li.button {\n    padding-right: 0;\n  }\n}\n/*# sourceMappingURL=data:application/json;base64, */"